<template>
	<view class="login-container">
		<view class="tip"><image src="../../static/images/tip.png"></image></view>
		<view class="login-section">
			<view class="logo-section"><image src="../../static/images/logo_login.png" mode=""></image></view>
			<view class="login-form-section">
				<view class="login-rows-block">
					<view class="row-label">账号</view>
					<view class="login-rows" :class="{activeAccount:seleRows=='account'}">
						<view class="row-icon acc-icon"></view>
						<input class="uni-input" type="text" placeholder="请输入账号" v-model="account" @focus="focusEnv('account')"/>
					</view>
				</view>
				<view class="login-rows-block">
					<view class="row-label">密码</view>
					<view class="login-rows" :class="{activePwd:seleRows=='pwd'}">
						<view class="row-icon pwd-icon"></view>
						<input class="uni-input" type="password" placeholder="请输入密码" v-model="pwd" @focus="focusEnv('pwd')"/>
					</view>
				</view>
				<view class="login-rows-block">
					<view class="row-label">验证码</view>
					<view class="login-rows very-rows" :class="{activeCode:seleRows=='code'}">
						<view class="row-icon code-icon"></view>
						<input class="uni-input" type="number" placeholder="请输入验证码" v-model="verycode" @focus="focusEnv('code')"/>
						<view class="code-btn">1264</view>
					</view>
				</view>
				<button class="login-btn button" @click="login">登陆</button>
				<view class="forget-rows"><text @click="goFindPwd">忘记密码?</text></view>
			</view>
		</view>
		
		<view class="wx-login-block" @click="wxLogin">
			<view class="wx-logo"><image src="../../static/images/ic_wx.png"></image></view>
			<view class="wx-txt">授权登陆</view>
		</view>
	</view>
</template>

<script>
	export default{
		data(){
			return{
				account:"",
				pwd:"",
				verycode:"",
				seleRows:""
			}
		},
		methods:{
			focusEnv(type){
				this.seleRows=type;
			},
			goFindPwd(){
				uni.navigateTo({
				    url: '../findPwd/index?id=123'
				});
			},
			async login(){
				const {account,pwd}=this;
				if(!account){
					this.showToast("请输入账号");
					return;
				}else if(!pwd){
					this.showToast("请输入密码");
					return;
				}
				
				let params={
					url:'/api/user/UserLogin',
					data:{
						account,
						pwd
					}
				}
				
				const result=await this.$http(params);
				console.log(result);
			},
			wxLogin(){
				uni.getProvider({
				    service: 'oauth',
				    success: (res)=> {
				        console.log(res.provider);
				        //支持微信、qq和微博等
				        if (res.provider.indexOf('weixin')) {
				            uni.login({
				              provider: 'weixin',
				              success: (loginRes)=> {
				  				console.log("App微信获取用户信息成功",loginRes);
				              },
				              fail:(res)=>{
				              	console.log("App微信获取用户信息失败",res);
				              }
				
				            })
				        }
				    }
				});
			}
		}
	}
</script>

<style lang="scss" scoped>
	.login-container{
		background-color: #EAEEFC;
		background-image: url("../../static/images/bg_login.png");
		background-repeat: no-repeat;
		background-size: contain;
		height: 100%;
		padding-top: 100rpx;
		box-sizing: border-box;
		position: relative;
		.tip{
			width: 252rpx;
			height: 82rpx;
			position: absolute;
			left: 50%;
			margin-left: -126rpx;
			top:40rpx;
		}
		.login-section{
			background: rgba(255,255,255,.5);
			width: 620rpx;
			padding: 60rpx 60rpx 96rpx 60rpx;
			box-sizing: border-box;
			margin:0 auto;
			border-radius: 40rpx;
			.logo-section{
				width: 142rpx;
				height: 174rpx;
				margin: 0 auto;
			}
			.login-form-section{
				.login-rows-block{
					margin-bottom: 40rpx;
					.row-label{
						font-size: 24rpx;
						color: #1F1F39;
					}
					.login-rows{
						margin-top: 20rpx;
						border-bottom: 1px solid #fff;
						padding-bottom:32rpx;
						display: flex;
						.row-icon{
							margin-right: 48rpx;
							width:48rpx;
							height: 48rpx;
						}
						.acc-icon{
							background: url('../../static/images/ic_acc.png') no-repeat;
							background-size: 100% 100%;
						}
						.pwd-icon{
							background: url('../../static/images/ic_pwd.png') no-repeat;
							background-size: 100% 100%;
						}
						.code-icon{
							background: url('../../static/images/ic_code.png') no-repeat;
							background-size: 100% 100%;
						}
						.uni-input{
							flex: 1;
							font-size: 26rpx;
							height: 48rpx;
							line-height: 48rpx;
						}
					}
					.activeAccount{
						border-bottom: 1px solid #8FA8FF;
						.acc-icon{
							background: url('../../static/images/ic_acc_sel.png') no-repeat;
							background-size: 100% 100%;
						}
					}
					.activePwd{
						border-bottom: 1px solid #8FA8FF;
						.pwd-icon{
							background: url('../../static/images/ic_pwd_sel.png') no-repeat;
							background-size: 100% 100%;
						}
					}
					.activeCode{
						border-bottom: 1px solid #8FA8FF;
						.code-icon{
							background: url('../../static/images/ic_code_sel.png') no-repeat;
							background-size: 100% 100%;
						}
					}
					.very-rows{
						position: relative;
						.code-btn{
							position: absolute;
							right: 0;
							width: 160rpx;
							height: 80rpx;
							text-align: center;
							line-height: 80rpx;
							border-radius: 14rpx;
							background: #ffe5e6;
							color: #ff94b4;
							top:-20rpx;
							font-size: 20rpx;
						}
					}
				}
				.login-btn{
					margin-top: 60rpx;
				}
				.forget-rows{
					text-align: right;
					margin-top: 40rpx;
					font-size: 24rpx;
					color: #666;
				}
			}
		}
		
		.wx-login-block{
			width: 150rpx;
			margin: 0 auto;
			margin-top: 40rpx;
			.wx-logo{
				width: 68rpx;
				height: 68rpx;
				margin: 0 auto;
				margin-bottom: 24rpx;
			}
			.wx-txt{
				text-align: center;
				color: #666;
			}
			
		}
	}
	@media screen and (min-height: 800px) {
		.login-container{
			padding-top: 200rpx;
			.tip{
				top:130rpx;
			}
			.wx-login-block{
				margin-top: 120rpx;
				.wx-logo{
					margin-bottom: 36rpx;
				}
			}
		}
	}
</style>
